import React, { useEffect, useRef } from 'react';
import Player from 'xgplayer'; // XGPlayer
import 'xgplayer/dist/index.min.css'; // XGPlayer
import { useLocation } from 'react-router-dom';

const XGPlayerComponent = ({ src }) => {
  const playerRef = useRef(null); // refDOM
  const location = useLocation();
  console.log(location);
  const { id, url } = location.state || {};
  console.log(id, url);
  useEffect(() => {
    if (playerRef.current) {
      const height = '40vh';
      // XGPlayerref.currentDOM
      const player = new Player({
        el: playerRef.current, // DOM
        url: url, // 
        width: '100%', // 
        height: `calc(100vh - ${height})`, // 
        playbackRate: [0.5, 0.75, 1, 1.5, 2], // 
        defaultPlaybackRate: 1, // 
        playsinline: true, // iPhone Safari
      });

      // 
      return () => {
        player.destroy();
      };
    }
  }, [src]); // src

  // divref
  return (
    <div
      ref={playerRef}
      id='xgplayer'
      style={{ width: '100%', height: '100%' }}
    ></div>
  );
};

export default XGPlayerComponent;
